.dp-po-header {
  background: $white;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.1);
  padding: 12px 0px;

  .dp-po-homelink {
    display: flex !important;
    align-items: center;
    text-decoration: none;

    img {
      max-height: 49px;
    }

    .dp-po-logo {
      position: relative;
      display: block;
      padding: 0 50px 0 0;

      &:after {
        position: absolute;
        content: "";
        width: 1px;
        height: 28px;
        background: $grey-light;
        right: 28px;
        top: calc(50% - 14px);
      }
    }

    &.d-sm-none {
      display: none !important;
    }
  }

  .dp-po-brand-name {
    @extend .h3;
    color: $text-color;
    position: relative;
    padding: 10px 0;
    margin: 0;
    line-height: 2.8;
  }

  .dp-po-header-right {
    display: flex;
    align-items: center;
    height: 100%;
    justify-content: flex-end;
  }

  .dp-po-user {
    position: relative;
    margin-left: 30px;

    &:before {
      content: "";
      height: 100%;
      width: 1px;
      background: $grey-light;
      position: absolute;
    }

    &-login {
      font-weight: 700;
      color: $text-color;
      font-size: $p2-font-size;
      padding: 10px 15px 10px 30px;
      display: flex;

      &:hover {
        color: $grey;
        text-decoration: none;
      }

      .dp-po-icon {
        color: $brand-primary;
        font-size: 20px;
      }

      &-figure {
        position: relative;
      }

      &-count {
        border-radius: 50%;
        background-color: $danger;
        height: 22px;
        width: 22px;
        color: $white;
        justify-content: center;
        align-items: center;
        display: flex;
        font-size: 14px;
        font-weight: 500;
        font-family: $font-family-heading;
        position: absolute;
        right: 0;
        top: -9px;
      }
    }

    &-dropdown {
      padding: 20px;

      .dp-po-social-login {
        display: block;

        .dp-po-oauth {
          margin: 0;
        }
      }

      .dp-po-divider {
        display: block;

        &-text {
          margin: 20px 0;
          text-align: center;

          &:before,
          &:after {
            height: 2px;
            width: 100px;
            top: 10px;
            left: 0;
          }

          &:after {
            left: auto;
            right: 0;
          }
        }
      }

      &-title {
        font-size: $p4-font-size;
        margin: 0 0 20px;
        padding: 0;
      }

      &.dp-po-logged-in-user {
        white-space: nowrap;

        a {
          color: var(--text-color);

          &:hover {
            color: var(--brand-primary);
            text-decoration: none;
          }
        }
      }
    }

    &-menu {
      min-width: 300px;

      &-list {
        margin: 20px 0 0;
        padding: 10px 0;
        border-top: 1px solid $grey-light;
      }

      &-item {
        list-style: none;
        padding: 10px 0;
      }

      &-link {
        display: flex;
        align-items: center;
        white-space: nowrap;
        @include typo($s19, 400, "head");
        color: $grey-darkest;

        &:hover {
          text-decoration: none;
          color: $brand-primary;

          .dp-po-user-menu-image {
            color: $brand-primary;
          }
        }
      }

      &-image {
        margin-right: 10px;
        width: 30px;
        height: 25px;
        fill: $brand-primary;
      }
    }
  }

  .dp-po-language {
    margin-right: 30px;

    &-link {
      color: $text-color;
      font-size: $p2-font-size;
      height: 30px;
      display: flex;
      align-items: center;
      border: none;
      background-color: transparent;

      &:hover {
        text-decoration: none;
        color: $brand-primary;
      }

      &-text {
        display: flex;
        align-items: center;
        line-height: 1;
      }

      img {
        margin-right: 10px;
      }

      .dp-po-icon {
        color: $brand-primary;
        font-size: 20px;
        margin-left: 10px;
      }
    }

    .dropdown-item {
      font-size: $p2-font-size;
    }
  }

  .dp-po-admin-menu {
    $self: ".dp-po-admin-menu";
    margin-left: 30px;

    &-link {
      display: flex;
      align-items: center;
      color: $grey-darkest;
      font-size: $p2-font-size;

      .dp-po-icon-svg {
        margin: 2px 10px 0 0;
        color: $grey;
      }

      .fa-angle-down {
        margin-left: 10px;
      }

      &[aria-expanded=true],
      &:hover {
        text-decoration: none;
        color: $brand-primary;

        .dp-po-icon-svg {
          color: $brand-primary;
        }
      }

    }

    .dp-po-icon-round {
      background: $grey;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      color: $white;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 10px;
    }

    hr {
      margin-top: 10px;
      margin-bottom: 10px;
    }

    &.show {
      #{$self}-link {
        .dp-po-icon-round {
          background: $brand-primary;
        }
      }
    }

    .dropdown-item {
      font-size: 15px;

      .dp-po-icon-svg {
        margin: 2px 10px 0 0;
        color: $grey;
      }

      &:hover {
        .dp-po-icon-svg {
          color: $brand-primary;
        }
      }
    }

    .dropdown-menu {
      width: 280px;
    }
  }

  .dp-po-admin-edit {
    margin-top: 10px;
    border-top: 1px solid $grey-light;
    padding-top: 6px;
    .dp-po-icon-svg {
      margin: 0 10px 0 0;
      color: $grey;
    }
  }

  .dp-po-admin-wrapper {
    display: flex;
    align-items: center;
    margin-right: 30px;
    position: relative;
    padding-right: 30px;

    &:after {
      content: "";
      height: 100%;
      width: 1px;
      background: #d3d6d7;
      position: absolute;
      right: 0;
    }
  }

  .dp-po-impersonation {
    position: relative;
    margin-right: 30px;
    padding-right: 30px;
    max-width: 230px;

    &-badge {
      margin: 0 0 5px;
      background: $purple;
      color: $white;
      @include typo($s12, 600, "body");
      display: flex;
      align-items: center;
      border-radius: 10px;
      padding: 2px 10px;

      .dp-po-icon {
        margin-right: 10px;
      }
    }

    &-user {
      margin: 0;
      font-size: $s12;
      padding: 0 25px 0 10px;
      align-items: center;
      width: 180px;
      text-overflow: ellipsis;
      overflow: hidden;
      white-space: nowrap;
      display: block;
      position: relative;
    }

    &-close {
      position: absolute;
      right: 10px;
      top: 1px;

      .dp-po-icon {
        color: $grey;
        font-size: 11px;

        &:hover {
          color: $danger;
        }
      }
    }

    &:after {
      content: "";
      height: 100%;
      width: 1px;
      background: #d3d6d7;
      position: absolute;
      right: 0;
      top: 0;
    }
  }
}
