@include media-breakpoint-down(sm) {
  .breadcrumb-outer {
    border: none;

    .container {
      height: auto;
    }
  }

  .breadcrumb-image {
    display: none;
  }

  .breadcrumb {
    padding: 0;
    width: 100%;
    border-bottom: 1px solid $grey-light;
  }

  .breadcrumb-item {
    white-space: nowrap;
    margin: 0;
    display: none;

    &:first-child {
      margin: 0;
    }

    a {
      text-overflow: ellipsis;
      overflow: hidden;
      margin-right: 21px;
    }
    &:nth-last-child(2) {
      display: flex;
      margin-left: 20px;
      .dp-po-icon {
        transform: rotate(180deg);
      }
    }
  }

