.dp-po-community-view {
  $self: '.dp-po-community-view';

  &-item {
    display: flex;
    border-top: 1px solid $grey-light;
    padding: 17px 0;
    flex-wrap: wrap;

    &-head {
      border: none;
      padding: 0;
      @include typo($s17, 500, 'head');
      color: $grey-dark;

      #{$self}-activity {
        text-align: center;
      }
    }

    &:last-child {
      padding-bottom: 0;
    }
  }

  &-likes {
    flex: 0 0 50px;
    margin-right: 20px;
  }

  &-title {
    flex: 0 0 calc(100% - 530px);
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    .dp-po-avatar {

      &-image,
      &-name {
        width: 24px;
        height: 24px;
        font-size: 12px;
        padding: 4px 0;
        font-weight: normal;
        display: inline-block;
      }
    }
  }

  &-link {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    flex: 0 0 100%;
    margin-bottom: 10px;
    @include typo($s19, 400, 'head');
    color: $grey-darkest;
  }

  &-time {
    font-size: $s12;
    margin-left: 10px;
    position: relative;
    top: 2px;
  }

  &-status {
    box-sizing: content-box;
    flex: 0 0 130px;
    padding-left: 20px;
  }

  &-comments {
    flex: 0 0 120px;
    text-align: center;
  }

  &-activity {
    box-sizing: content-box;
    flex: 0 0 160px;
    padding: 0 10px;

    &-list {
      display: flex;
      margin: 0;
      padding: 0;
      flex-direction: row-reverse;
    }

    &-item {
      display: block;
      list-style: none;
      font-size: $s12;

      text-align: center;
      flex: 0 0 34px;

      &:first-child {
        margin-right: auto;
      }
    }

    &-image {
      border-radius: 50%;
      width: 40px;
      height: 40px;
      margin-bottom: 8px;
      border: 5px solid $white;
      display: inline-block;
      background-size: cover;
    }

    &-name {
      border-radius: 50%;
      width: 40px;
      height: 40px;
      margin-bottom: 8px;
      border: 5px solid $white;
      display: inline-block;
      background: var(--brand-primary-l-350);
      color: $grey-darkest;
      font-weight: normal;
      padding: 7px 0;
      text-align: center;
      font-size: $s12;
    }


    &-link {
      color: $grey-dark;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;

      &:hover {
        text-decoration: none;

        #{$self}-activity-name,
        #{$self}-activity-image {
          position: relative;
          z-index: 10;

          &:before {
            content: " ";
            position: absolute;
            z-index: -1;
            top: -2px;
            left: -2px;
            right: -2px;
            bottom: -2px;
            border: 2px solid $brand-primary;
            border-radius: 50%;
          }
        }
      }
    }

  }

  &-official {
    flex: 0 0 calc(100% - 70px);
    position: relative;
    background: $grey-lightest;
    border-radius: 4px;
    padding: 10px;
    margin-left: 70px;
    margin-top: 15px;

    &-time {
      position: absolute;
      right: 10px;
      top: 10px;
      color: $grey-dark;
      font-size: $s15;
    }

    &-wrap {
      display: flex;
      align-items: flex-start;

      img {
        max-width: 20px;
        height: auto;
        margin-right: 10px;
      }
    }

    &-author {
      border-radius: 50%;
      margin-top: 12px;
    }

    &-title {
      margin: 0;
      @include typo($s17, 400, 'head');
      color: $grey-dark;
    }

    &-response {
      font-size: $s15;
      line-height: 1.5;
      margin: 10px 0 0;

    }
  }

  &-compact {
    #{$self}-item {
      align-items: center;
    }

    #{$self}-likes {
      flex: 0 0 80px;
    }

    #{$self}-title {
      flex: 0 0 auto;
      width: calc(100% - 640px);
    }

    #{$self}-office {
      flex: 0 0 49px;
      text-align: center;
      position: relative;

      img {
        max-width: 25px;
      }

      &-reply {
        position: absolute;
        background: $white;
        box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.15);
        border-radius: 2px;
        border: 1px solid $grey-lighter;
        width: 500px;
        z-index: 1;
        text-align: left;
        padding: 15px;

        &-title {
          @include typo($s15, 500, 'head');
        }

        &-desc {
          font-size: $s16;
          line-height: 1.5;
          margin: 0 0 10px;
        }

        &-list {
          margin: 0;
          padding: 0;
          display: flex;
          align-items: center;
          justify-content: flex-end;
        }

        &-item {
          list-style: none;
          position: relative;
          font-size: $s16;
          margin: 0 0 0 20px;

          &:after {
            content: '';
            width: 1px;
            height: 100%;
            position: absolute;
            background: $grey-light;
            right: -10px;
            top: 0;
          }

          &:last-child {
            color: $grey;

            &:after {
              display: none;
            }
          }
        }
      }
    }

    #{$self}-link {
      margin: 0;
    }

    #{$self}-comments {}

    #{$self}-author {
      flex: 0 0 70px;
      text-align: center;
      height: 30px;

      .dp-po-avatar {
        justify-content: center;

        &-image {
          margin: 0;
        }
      }


      &-link {
        display: inline-block;

        &:hover {
          img {
            border-color: $brand-primary !important;
          }
        }
      }

      .dp-po-avatar-name {
        margin: 0;
      }
    }

    #{$self}-activity {
      flex: 0 0 130px;

      &-list {}

      &-item {
        flex: inherit;

        &:first-child {
          #{$self}-activity-image {
            margin-left: 0;
          }
        }
      }

      &-image,
      &-name {
        margin: 0 -8px 0 0;
      }
    }
  }

  &-statuschange {
    #{$self}-title {
      width: calc(100% - 620px);
      flex: 0 0 auto;
    }

    #{$self}-status {
      flex: 0 0 300px;
    }

    #{$self}-official {
      margin-left: 0;
      flex: 0 0 100%;
    }
  }

  .no-data {
    text-align: center;
    display: block;
    width: 100%;
    font-size: $s23;
    border-top: 1px solid $grey-light;
    padding-top: 27px;
    .dp-po-icon {
      color: $danger;
      margin-right: 6px;
      vertical-align: -4px;
    }
  }
}


.community-container {
  display: flex;
  width: 100%;
}

.community-flex-container {
  width: calc(100% - 40px);
}

@media (max-width: 576px) {
.community-flex-container {
  flex-direction: column;
}
}
