/*
Styles which are out of .dp-po-* scope
e.g. Modal Backdrop
*/
.modal-backdrop {
  background: var(--text-color);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1040;
  width: 100vw;
  height: 100vh;
}
.modal-backdrop.show {
  opacity: 0.5;
}

img[class$=-svg] {
  display: none;
}

body.portal-mode-focus-window header.dp-po-header {
  display: none;
}
body.portal-mode-focus-window section.dp-po-search {
  display: none;
}
body.portal-mode-focus-window .breadcrumb-outer {
  display: none;
}
body.portal-mode-focus-window .dp-po-notification {
  display: none;
}
