  white-space: nowrap;
}

body {
  margin: 0;
  padding: 0;
  font-size: 1.125rem;
  color: var(--text-color);
}
body .screen-reader {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  padding: 0;
  position: absolute;
  white-space: nowrap;
  width: 1px;
  font-family: var(--font-family-sans-serif);
}
body .screen-reader-focusable:active, body .screen-reader-focusable:focus {
  clip: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  white-space: normal;
  width: auto;
}
body.mobile-modal-open {
  overflow: hidden;
}

h1.aria-hide {
  position: absolute !important;
  top: -9999px !important;
  left: -9999px !important;
}