.dp-po-table {
  $self: '.dp-po-table';
  margin: 20px -30px 0;

  &-row {
    display: flex;
    align-items: center;
    flex-flow: row wrap;
    padding: 0 30px;
    position: relative;

    &-head {
      font-weight: 700;

      #{$self}-cell {
        color: $grey-dark;
        display: flex;

        span {
          text-overflow: ellipsis;
          overflow: inherit !important;
        }

        a {
          color: $grey-dark;
          &:hover,
          &.active {
            color: $brand-primary;
          }
        }

        .dp-po-icon {
          margin-left: 10px;
        }
      }

      &:hover {
        background: transparent !important;

        #{$self}-cell {
          color: $grey-dark !important;
        }
      }
    }

    &:nth-child(even) {
      background: $grey-lightest;
    }

    &:hover {
      background: $info-light;

      #{$self}-cell-link {
        color: $brand-primary;
      }
    }
  }

  &-col2 {
    #{$self}-cell {
      width: 80%;
    }
    #{$self}-cell:nth-child(2) {
      width: 20%;
    }
  }

  &-col3 {
    #{$self}-cell {
      width: calc(100% / 3);
    }
  }

  &-col4 {
    #{$self}-cell {
      width: calc(100% / 4);
    }
  }

  &-col5 {
    #{$self}-cell {
      width: calc(100% / 5);
    }
  }

  &-col6 {
    #{$self}-cell {
      width: calc(100% / 6);
    }
  }

  &-col7 {
    #{$self}-cell {
      width: calc(100% / 7);
    }
  }

  &-cell {
    width: calc(100% / 6);
    font-size: $p3-font-size;
    padding: 15px 5px;
    white-space: nowrap;
    overflow: hidden !important;
    span {
      text-overflow: ellipsis;
    }

    &-link {
      color: $text-color;
    }
  }
}
